import React, { Component } from 'react';
import { Modal, Table, Checkbox, Button, List, Form, Input, DatePicker } from 'antd';
import store from './store';
import store2 from "../process/store";
import { observer } from 'mobx-react';
import exportFile from '../../helpers/export-file';
import hasErrors from "../../helpers/has-errors";
import request from "../../helpers/request";
import commonFormProps from '../../config/common-form';

let formProps = {
    labelCol: {
        span: 4,
        gutter: 16,
    },
    wrapperCol: {
        span: 18,
        gutter: 16,
    },
};
let file_type = ['', 'A', 'B', 'C', 'D', 'E'];
@observer
class CreateFileC extends Component {
  columns = [
    {
      title: '',
      dataIndex: 'number',
    },
    {
      title: '',
      dataIndex: 'user',
    },
    {
      title: '',
      dataIndex: 'create_time',
    },
    {
      title: '',
      dataIndex: 'file_type',
      render: text => {
        return <span>{file_type[text]}</span>;
      },
    },
    {
      title: '',
      dataIndex: 'file_name',
      render: (text, record) => (
        <span>
          <a onClick={() => this.handleExportFile(record.file_id)}>{text}</a>
        </span>
      ),
    },
    {
      title: '',
      dataIndex: '',
      render: (text, record) => {
        return <Checkbox onChange={e => this.handleCheckChange(e, record)} />;
      },
    },
  ];
  render() {
    let { visible, setVisible, dataSource } = this.props;
    let {resTableC} = store;
    // let { file_id } = dataSource;
    let fileCVisible = visible.file_c;
    let { getFieldDecorator, getFieldValue } = this.props.form;
    let data = dataSource.filter(t => t.file_type == 2)
    let Footer = () => (
      <footer style={{ textAlign: 'right' }}>
        <Button type="primary" disabled={isDisabledBtn()} onClick={this.createC}></Button>
      </footer>
    );
    let ModalFooter = () => (
      <React.Fragment>
        <Button onClick={() => setVisible(false)}></Button>
        <Button type="primary"  onClick={this.handleConfirm}>
          
        </Button>
      </React.Fragment>
    );
      let isDisabledBtn = () => {
          if(!getFieldValue('create_time')){
              return true
          }
          return false
      }
      console.log(isDisabledBtn())

      return (
      <Modal
        visible={fileCVisible}
        title="C"
        width={800}
        footer={<ModalFooter />}
        onCancel={() => setVisible(false)}
      >
        <Table
          title={() => (
                <Form>
                    <Form.Item label="100" {...formProps}>
                        <Input disabled={true} defaultValue={this.props.wf_id} />
                    </Form.Item>
                    <Form.Item label="" {...formProps}>
                        {
                            getFieldDecorator('create_time',{
                                rules: [{
                                    required: true,
                                    message: ''
                                }]
                            })(<DatePicker />)
                        }
                    </Form.Item>
                </Form>
          )}
          pagination={{defaultPageSize:8}}
          dataSource={data}
          columns={this.columns}
          size="small"
          bordered={false}
          footer={Footer}
          rowKey="file_id"
        />
        <div><p>:{resTableC.msg}</p></div>
          {!resTableC.table ? null :
              <div>
              <p>
                  {resTableC.table[0].rowNumber}:{resTableC.table[0].rowdata1}<br/>
                  {resTableC.table[0].rowNumber}:{resTableC.table[0].rowdata2}<br/>
                  {resTableC.table[1].rowNumber}:{resTableC.table[1].rowdata1}<br/>
                  {resTableC.table[1].rowNumber}:{resTableC.table[1].rowdata2}
              </p>
          </div>}
      </Modal>
    );
  }
  handleCheckChange = (e, record) => {
    let { file_id } = record;
    let { file_ids } = store;
    let allFile_Ids = Array.from(file_ids);
      if (e.target.checked) {
        allFile_Ids.push(file_id);
    } else{
          allFile_Ids.splice(allFile_Ids.indexOf(file_id), 1);
    }
    store.setFileIds(allFile_Ids);
  };
  handleExportFile = file_id => {
    let { id } = this.props;
    exportFile({
      url: '/api/get_file',
      data: {
        id: id,
        file_id: file_id,
      },
    });
  };
  createC = ()=>{
      let { getFieldsValue, getFieldsError } =this.props.form;
      let values = getFieldsValue();
      let {create_time} = values;
      create_time = create_time.format('YYYY-MM-DD');
      console.log(create_time)
      let {file_ids} = store;
      let id = this.props.id;
      let all_ids = file_ids.toString();
      let canCreate = !hasErrors(getFieldsError());
      if(canCreate){
        request({
            url:'/api/create_file_c',
            data:{
                id,
                create_time,
                file_ids: all_ids
            },
            success: res => {
                store.setTable(res);
                this.fetchProcessList();
            },
            fail: res => {
                store.setTable(res);
                this.warning(res)
            }
        })
  }
}
    warning = (res) => {
        Modal.warning({
            title:'',
            content: res.msg
        })
    }
handleConfirm = ()=> {
      this.fetchProcessList();
      this.props.setVisible(false);
}
    fetchProcessList = () => {
        request({
            url:'/api/get_file_list',
            data:{
                id:this.props.id,
                keyword:'',
                file_type:'',
            },
            success: ({table}) => {
                store2.setProcessList(table);
            }
        })
    }

}
export default Form.create()(CreateFileC);
