import React, { Component } from 'react';
import { Modal, Form, Select, Upload, Button, Table, message, Input } from 'antd';
import request from '../../helpers/request';
import hasErrors from '../../helpers/has-errors';
import store from '../process/store';
let Option = Select.Option;
let formProps = {
  labelCol: {
    span: 4,
    gutter: 16,
  },
  wrapperCol: {
    span: 18,
    gutter: 16,
  },
};
let standards = {
  input: {
    dataSource: [
      {
        phone: '1234567890',
        code: '-',
        operate_time: '-',
        result: 'SUCCESS',
        error: '',
      },
      {
        phone: '1234567890',
        code: '-',
        operate_time: '-',
        result: '-',
        error: '',
      },
    ],
    columns: [
      {
        title: '',
        dataIndex: 'phone',
      },
      {
        title: '',
        dataIndex: 'code',
      },
      {
        title: '',
        dataIndex: 'operate_time',
      },
      {
        title: '',
        dataIndex: 'result',
      },
      {
        title: '',
        dataIndex: 'error',
      },
    ],
  },
  output: {
    dataSource: [
      {
        order: 1,
        phone: 1234567890,
        code: '',
        operate_time: '',
        result: '',
        error: '',
      },
      {
        order: 2,
        phone: 1234567890,
        code: '',
        operate_time: '',
        result: '',
        error: '',
      },
    ],
    columns: [
      {
        title: '',
        dataIndex: 'order',
      },
      {
        title: '',
        dataIndex: 'phone',
      },
      {
        title: '',
        dataIndex: 'code',
      },
      {
        title: '',
        dataIndex: 'operate_time',
      },
      {
        title: '',
        dataIndex: 'result',
      },
      {
        title: '',
        dataIndex: 'error',
      },
    ],
  },
};
let tableProps = {
  size: 'small',
  pagination: false,
};
class CreateFileE extends Component {
  state = {
    fileList: [],
  };
  uploadProps2 = {
    onChange: ({ fileList }) => {
      this.setState({
        fileList,
      });
    },
    beforeUpload: () => false,
    onRemove: () => {
      this.props.form.setFieldsValue({
        file: null,
      });
      this.setState(
        {
          fileList: [],
        },
        () => {
          this.props.form.validateFields();
        }
      );
    },
  };
  render() {
    let { visible, setVisible, form } = this.props;
    let { getFieldDecorator, getFieldValue } = form;
    let { fileList } = this.state;
    let fileEVisible = visible.file_e;
    let ModalFooter = () => (
      <React.Fragment>
        <Button onClick={() => setVisible(false)}></Button>
        <Button type="primary" disabled={isDisabledBtn()} onClick={this.createE}>
          
        </Button>
      </React.Fragment>
    );
      let isDisabledBtn = () => {
          if(!getFieldValue('create_time')){
              return true
          }
          return false
      }
    return (
      <Modal
        title="E"
        visible={fileEVisible}
        footer={<ModalFooter />}
        onCancel={() => setVisible(false)}
        width={800}
      >
        <Form>
          <Form.Item label="" {...formProps}>
            <Select defaultValue="D">
              <Option value="D">D</Option>
            </Select>
          </Form.Item>
          <Form.Item label={<span />} colon={false} {...formProps}>
            {getFieldDecorator('file', {
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            })(
              <Upload {...this.uploadProps2} fileList={fileList} multiple={true}>
                <Button></Button>
              </Upload>
            )}
          </Form.Item>
          <Form.Item label="" {...formProps}>
            <Table rowKey="result" {...standards.input} {...tableProps} />
          </Form.Item>
          <Form.Item label="" {...formProps}>
            <Table rowKey="result" {...standards.output} {...tableProps} />
          </Form.Item>
          <Form.Item label="" {...formProps}>
            {getFieldDecorator('create_time', {
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            })(<Input />)}
          </Form.Item>
        </Form>
      </Modal>
    );
  }
  createE = () => {
    let { id } = this.props;
    let { getFieldsValue, getFieldsError } = this.props.form;
    let canCreate = !hasErrors(getFieldsError());
    let values = getFieldsValue();
    let { file, create_time } = values;
    let { fileList } = file;
    let fileList1 = fileList[0];
    let fileList2 = values.file.fileList[1];

    if (canCreate) {
      // if (validateName1 && validateName2) {
      // let formData = new FormData();
      // formData.append('file',fileList1);
      // formData.append('file',fileList2);
      // formData.append('id',id);
      request({
        url: '/api/create_file_e',
        data: {
          id,
          create_time,
          file: fileList,
        },
        postType: 'formdata',
        success: data => {
          this.props.setVisible(false);
          this.fetchProcessList();
        },
        fail: data => {
          message.error('');
          this.props.store.setCreateVisible(false);
        },
      });
    }
    // }
  };
  fetchProcessList = () => {
    request({
      url: '/api/get_file_list',
      data: {
        id: this.props.id,
        keyword: '',
        file_type: '',
      },
      success: ({ table }) => {
        store.setProcessList(table);
      },
    });
  };
}
export default Form.create()(CreateFileE);
